.road {
    height: 60px;
    width: 100%;
    border-top: 10px solid green;
    border-bottom: 10px solid green;
    margin: 0 0 20px 0;
    position: relative;
}

.lines {
    height: 100%;
    display: grid;
    grid-template-columns: auto auto auto auto auto;
    justify-content: space-around;
    align-content: center;
    background-color: gray;
}

.lines div {
    width: 100px;
    height: 10px;
    background-color: rgb(214, 163, 163);
}
.car {
    position: absolute;
    left: 0px;
    width: 70px;
    height: 60px;
    background-color: lightblue;
    opacity: .5;
    transition: all 1s linear

}
.car img {
    height: 60px;
}
.carName p {
    position:absolute;
    bottom: 55px;
}
.finishLine {
    width: 2px;
    height: 100%;
    position: absolute;
    left: 1000px;
    background-color: black;
}