.App-header {
    background-color: #1c3463;
    min-height: 10vh;
    font-size: calc(10px + 2vmin);
    color: white;
    display: grid;
    align-content: center;
    justify-content: center;
    margin: 0 0 20px 0;
  }

.App-header button:nth-child(1) {
  position: absolute;
  left: 20px;
  top: 20px;
  width: 80px;
  height: 40px;
  border-radius: 5px;

}

.App-header button:nth-child(2) {
  position: absolute;
   right: 20px;
   top: 20px;
   width: 80px;
   height: 40px;
   border-radius: 5px;

}